
:root {
  --white: 246,246,246;
  --grey: 25, 28, 31;
  --blue: 99, 181, 236;
  --darkblue: 60, 111, 146;
  --green: 199, 244, 100;
  --pink: 255, 66, 79;
  --red: 196,77,88;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Muli', Helvetica, Arial, sans-serif;
}
html {
  font-size: 62.5%;
}
body {
  font-size: 1.6rem;
}
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.home{
  display: grid;

  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100%;

  grid-template-rows: 5rem auto;
  grid-template-columns: 25rem 1fr;
  grid-template-areas: 
  "search search"
  "navi movies"
}
.search {
  grid-area: search;
  justify-content: start;
}
.navi {
  grid-area: navi;
  overflow-y: scroll;
}
.movies {
  display: grid;
  grid-area: movies;
  grid-template-columns: repeat(auto-fill, minmax(22rem, 1fr));
  grid-gap: 2rem;
  justify-items: center;

  grid-row-gap: 2rem;

  overflow-y: scroll;
  overflow-x: hidden;

  position: relative;
}
.movies__headline {
  grid-column: 1 / -1;
  color: rgb(var(--grey));
  justify-self: start;
  font-weight: 200;
  padding: 5rem 1.5rem;
}
.movies__headline--loading {
  justify-self: center;
}
.btn {
  padding: .5rem 3rem;
  background: rgb(var(--white));
  color: rgb(var(--pink));
  border: none;
  border-radius: 5px;
  text-decoration: none;
  font-weight: 600;
  display: inline-block;
  box-shadow: .4rem .4rem 0 rgb(var(--red));
  cursor: pointer;
  backface-visibility: hidden;
  transition: all .1s ease;
}
.btn:hover {
  transform: translateY(-.2rem);
  box-shadow: .4rem .6rem 0 rgb(var(--red));
}
.btn:active {
  transform: translateY(.1rem);
  box-shadow: .2rem .2rem 0 rgb(var(--red));
}
.btn--blue {
  color: rgb(var(--darkblue));
  box-shadow: .4rem .4rem 0 rgb(var(--darkblue));
}
.btn--blue:hover {
  box-shadow: .4rem .6rem 0 rgb(var(--darkblue));
}
.btn--blue:active {
  box-shadow: .2rem .2rem 0 rgb(var(--darkblue));
}


